

*{
  font-family: Helvetica;
}

body{
  padding: 60px;
}
.card-grid{
  display: flex;
  flex-wrap: wrap;
}
.card{
  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.card p{
  padding: 5px;
  text-align: center;
}

.card img{
  min-height: 70px;
}













